o voluntario, sino de un instrumento con carga normativa: lo que las partes pactan, dentro de los límites de la Ley, se convierte en norma vinculante para ellas (Reale, 1984). Esta es la base de la autonomía privada, reconocida en el ordenamiento colombiano, en normas como el Artículo 16 del Código Civil.")])])]),_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 order-2"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema2/5.png")}})])]),_c('div',{staticClass:"col-lg-7 order-1 mb-3 mb-lg-0"},[_c('h5',[_vm._v("Tercer tema")]),_c('p',[_vm._v("El negocio jurídico tiene elementos esenciales que garantizan su validez: consentimiento, objeto, causa y capacidad. También existen elementos accidentales, como la condición, el plazo y el modo, que no son esenciales pero modifican el negocio si se pactan.")])])])])],1)]),_vm._m(1),_vm._m(2),_vm._m(3),_c('p',{staticClass:"mb-4"},[_vm._v("Todo negocio jurídico, sin importar su tipo o complejidad, debe construirse sobre cuatro pilares fundamentales: la capacidad de los sujetos, el consentimiento, el objeto y la causa. Estos elementos no son negociables. Si falta alguno, el negocio simplemente no existe o es inválido.")]),_c('div',{staticClass:"row align-items-center mb-5"},[_vm._m(4),_c('div',{staticClass:"col-lg-8"},[_c('p',{attrs:{"data-aos":"fade-left"}}),_c('LineaTiempoD',{staticClass:"color-primario large",attrs:{"data-aos":"fade-left"}},[_c('p',{attrs:{"numero":"1","titulo":"El primer elemento es la capacidad jurídica de los sujetos"}},[_vm._v("Solo las personas que tengan plena aptitud legal pueden obligarse por sí mismas. En Colombia, según el Artículo 1502 del Código Civil, toda persona es legalmente capaz, salvo que la Ley disponga lo contrario. Esto significa que menores de edad, personas interdictas o con discapacidad legalmente reconocida, necesitan de un representante legal para celebrar negocios jurídicos. Un contrato firmado por quien no tiene capacidad es co